	  activityName: 'Cultural Foods',
	  theActivity: 'Malcolm the Robots eyes light up, I love to travel and try new cuisine from all over the world! During my travels, Ive learned a lot about other cultures and their languages. Can you help me prepare a Hispanic meal? Help the robot by finding three items from the store: rice, carrots, and soup. After you do so, the Robot will tell you how to say them in Spanish.',
