	  exhibitName: 'Medical and Vet',
	  activityName: 'Going to the Doctor',
	  theActivity: 'Malcolm the Robot doesnt know about the human body. He needs to know about muscles, nerves, and organs. Malcolm says, I want to help people feel better. When I tell you the symptoms, help me identify the body part where the symptoms come from.<br/>Problem: It hurts you to swallow. <br/>Answer: Throat<br/>Problem: You have diarrhea and cramps.<br/>Answer: Intestines <br/>Problem: You are coughing and have trouble breathing.<br/>Answer: Lungs',
	  extendingTheActivity: 'You may come up with more problems for the child to answer, if these seem too easy. To further their knowledge, you can explain medicines that help with these symptoms. It may help with understanding to point out an overarching illness that may explain the symptoms (i.e. strep throat). You should also point out and identify where these parts are on yourself and the child.',
	  whatChildrenLearn: 'This activity helps children understand their bodies more by identifying where the part is on the skeleton and then locating it on themselves. This is a mapping task, which is important in childrens development. Problem solving is also an important skill for children to develop. By being able to determine which body part portrays the symptom, children are also identifying which body part it cannot be, which supports deductive reasoning.',
